const SEND_MESSAGE = 'ADD-MESSAGE';

let initialState = {
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'Hello'},
        {id: 3, message: 'Yo'},
        {id: 4, message: 'Gre!et'},
        {id: 5, message: 'Nihao'},
        {id: 6, message: 'chao'},
    ],

    dialogs: [
        {id: 1, name: 'Dmitriy'},
        {id: 2, name: 'Alexey'},
        {id: 3, name: 'Ivan'},
        {id: 4, name: 'Joseph'},
        {id: 5, name: 'Sergey'},
        {id: 6, name: 'Ilona'},
    ],
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody;
            return{
                ...state,
                messages: [...state.messages, {id: 7, message: body}],
            };
        default:
            return state;
    }
};

export const sendMessageCreator = (newMessageBody) => {
    return {type: SEND_MESSAGE, newMessageBody};
};

export default dialogsReducer;